import './App.css';
import { useState } from 'react';
import { ethers } from 'ethers';
import { SiTwitter } from 'react-icons/si';
import { SiDiscord } from 'react-icons/si';
import FlyFrogs from './artifacts/contracts/FlyFrogs.sol/FlyFrogs.json';
import frog1 from './images/burger.png';
import frogFlyFrog from './images/flyfrog.png';
import frogMollcule from './images/mollcule.png';
import frogRizzle from './images/rizzle.png';
import frogChupacabra from './images/tadpoleon.png';
import frogJigglyjams from './images/jigglyjams.png';
import frogPond from './images/pond.png';
import tadpolesTeaser from './images/tadpoles.jpg';

// Update with the contract address logged out to the CLI when it was deployed
const flyfrogsAddress = "0x31d4Da52c12542AC3D6aAdbA5ed26a3A563a86Dc";
const openSeaLink = "https://opensea.io/";
const etherscanLink = "https://etherscan.io/token/0x31d4Da52c12542AC3D6aAdbA5ed26a3A563a86Dc?a=";

function App() {
  const [frogIds, setFrogIds] = useState([]);
  const [account, setAccount] = useState();
  
  // request access to the user's MetaMask account
  async function getAccount() {
    if (typeof window.ethereum !== 'undefined') {
      const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      setAccount(account);
      getFlyFrogs();
    }
  }

  async function getFlyFrogs() {
    if (typeof window.ethereum !== 'undefined') {
      const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(flyfrogsAddress, FlyFrogs.abi, provider);
      let frogIds = [];
      try {
        const balance = await contract.balanceOf(account);
        for (let tokenIndex = 0; tokenIndex < balance; tokenIndex++) {
            const tokenId = await contract.tokenOfOwnerByIndex(account, tokenIndex);
            frogIds.push(tokenId.toString());
        }
      } catch (e) {
        console.log(e);
      }
      setFrogIds(frogIds);
    }
  }
  
  const renderFrog = (frog, src, includeName) => {
    return (
      <li key={frog}>
        <a href={openSeaLink+'assets/'+flyfrogsAddress+"/"+frog} target="_blank" rel="noreferrer" className="App-link">
          <img src={src} alt={"Fly Frog "+frog} width="200" />
          {includeName ? `Fly Frog ${frog}` : null}
        </a>
      </li>
    )
  }
  
  const  renderFrogs = frogs => {
    let message = `Congrats! You are the proud owner of ${frogs.length} Fly Frog.`
    if (parseInt(frogs.length) > 1) {
      message = `Congrats! You are the proud owner of ${frogs.length} Fly Frogs.`
    }
    const images = frogs.map((x) => renderFrog(x, "https://gateway.pinata.cloud/ipfs/QmTubGm6aKcjgTutgk7whbhXKeW42dHQmdXwp4GbbWxtTF/flyfrog"+x+".png", true));
    return (
      <div className="Frog-pond">
        <p>
          {message} <a className="App-link" href={etherscanLink+account} target="_blank" rel="noreferrer">View on Etherscan →</a>
        </p>
        <ul className="Frog-pond-list Frog-img-list">{images}</ul>
      </div>);
  }

  return (
    <div className="App">
      <header className="App-header">
        <div className="App-container">
          <div className="flex">
            <div className="flex-item">
              <button onClick={getAccount}>{account ? account.substring(0, 6) + '...' + account.substring(38) : 'Connect Wallet'}</button>
              <h1 className="App-heading">Fly Frogs</h1>
              <p>
                The future is amphibious, ribbit.<br />
                <strong>10,000 Frogs minted!</strong>
              </p>
            </div>
            <div className="flex-item">
              <img src={frog1} alt="Fly Frog with burger hat" />
            </div>
          </div>
        </div>
      </header>
      <section className="App-mint">
        <div className="App-container">
          <div>Sold out!?! Thanks for your support. View on <a href="https://opensea.io/collection/fly-frogs" target="_blank" rel="noreferrer" className="App-link">OpenSea</a>, ribbit.</div>
          {frogIds.length > 0 ? renderFrogs(frogIds) : ''}
        </div>
      </section>
      <section className="App-about">
        <div className="App-container">
          <h2>Come on get hoppy!</h2>
          <p>
            Fly Frogs are a collection of 10,000 randomly generated NFTs. No two frogs are exactly alike. The frogs are composed of 180+ hand-drawn assets across 6 traits:
          </p>
          <ul>
            <li>backgrounds (25)</li>
            <li>skins (23)</li>
            <li>heads (52)</li>
            <li>eyes (14)</li>
            <li>mouths (17)</li>
            <li>outfits (56)</li>
          </ul>
          
          <h2>The Pond</h2>
          <img src={frogPond} alt="Fly Frog Pond" />
          <p>Ah, <a href="http://discord.gg/kfp4xftqKM" target="_blank" className="App-link" rel="noreferrer">the pond</a>. Where there is no floor (just mud) and paper dissolves.</p>
          <p>We&rsquo;ve got a hopping, positive community. Tune into live drawing sessions with our artist and participate in doodle contests &mdash; it&rsquo;s a demhopcracy!</p>
          <p>Hop on over to <a href="http://discord.gg/kfp4xftqKM" target="_blank" className="App-link" rel="noreferrer">Discord</a> to join the Fly Frog community!</p>
          
          <h2>Where we&rsquo;re going...</h2>
          <p>...we don&rsquo;t need toads.</p>
          <div><img src={tadpolesTeaser} alt="Tadpoles" /></div>
          <p>So what&rsquo;s next? Bundles of pure tadpole joy. And you know what they say: every generation gets better looking.</p>
          
          <h2>Bifrographies</h2>
          <ul className="team">
            <li>
              <div><img src={frogFlyFrog} alt="Fly Frog" /></div>
              <div>
                <h4>Fly Frog</h4>
                Frog doodler
                <p>
                  Stays up at night drawing frogs while laughing out loud. Toy inventor by day, bringing joy to kids and parents around the world. Fell hard for that farkle in Mollcule&rsquo;s eye and married her.
                </p>
                <a href="https://twitter.com/FlyFrogsNFT" target="_blank" rel="noreferrer"><SiTwitter /></a>
              </div>
            </li>
            <li>
              <div><img src={frogMollcule} alt="Mollcule" /></div>
              <div>
                <h4>Mollcule</h4>
                Amphibious coder
                <p>Developer turned stay-at-home mom, singing “Wheels on the Bus” by day and coding frogs by night (and naptime). Plays a mean mahjong. Wife of Fly Frog.</p>
                <a href="https://twitter.com/mfnkl" target="_blank" rel="noreferrer"><SiTwitter /></a>
              </div>
            </li>
            <li>
              <div><img src={frogChupacabra} alt="Chupacabra" /></div>
              <div>
                <h4>Chupacabra</h4>
                Hype man and memer
                <p>A Calvin and Hobbes addict and not a legend of folklore, but rather a high school English teacher during the day, a husband, and father of two honey badgers.</p>
                <a href="https://twitter.com/ChupacabraPower" target="_blank" rel="noreferrer"><SiTwitter /></a>
              </div>
            </li>
            <li>
              <div><img src={frogRizzle} alt="Rizzle" /></div>
              <div>
                <h4>Rizzle</h4>
                Lead amphibian enthusiast
                <p>NFT innovator, podcaster, virtual event host, obnoxious twitter presence, lover of funny eyeballs and all things weird.</p>
                <a href="https://twitter.com/NFTland" target="_blank" rel="noreferrer"><SiTwitter /></a>
              </div>
            </li>
            <li>
              <div><img src={frogJigglyjams} alt="Jigglyjams" /></div>
              <div>
                <h4>Jigglyjams</h4>
                Bot builder
                <p>
                  Happy bots make for a happy pond. Can also wrangle a circuit board, climb rocks and make a fine omelette.
                </p>
                <a href="https://twitter.com/jjjjigglyjams" target="_blank" rel="noreferrer"><SiTwitter /></a>
              </div>
            </li>
          </ul>
          
          <h2>Save the Frogs</h2>
          <p>5% of profits from the initial sale will go to <a href="https://savethefrogs.com/" target="_blank" rel="noreferrer" className="App-link">Save the Frogs</a>!</p>
        </div>
      </section>
      <footer className="App-footer">
        <div className="App-container">
          <a href="https://etherscan.io/address/0x31d4Da52c12542AC3D6aAdbA5ed26a3A563a86Dc" target="_blank" rel="noreferrer">Smart Contract</a>
          <a href="https://twitter.com/FlyFrogsNFT" target="_blank" rel="noreferrer"><SiTwitter /></a>
          <a href="http://discord.gg/kfp4xftqKM" target="_blank" rel="noreferrer"><SiDiscord /></a>
          <a href="https://opensea.io/collection/fly-frogs" target="_blank" rel="noreferrer">OpenSea</a>
        </div>
      </footer>
    </div>
  );
}

export default App;